*{
    padding: 0;
    margin: 0;
}

.main{
    width: 100vw;
    height: 100vh;
}

.notesSection, .noteFormOff, .noteFormOn{
    inset: 0 0 0 100px;
    position: absolute;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
}

.noteFormOn, .noteFormOff{
    z-index: 100;
    flex-direction: column;
    background-color: rgb(196, 175, 175);
    gap: 15px;
}
.noteFormOn input{
    font-size: 15px;
}

.noteFormOff{
    display: none;
}

.notesSection{
    overflow-y: scroll;
    overflow-x: hidden;
}

.note{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 90%;
    padding: 5px;
    gap: 2px;
    height: 30%;    
    border: 1px solid grey;
    margin: 15px;
}



.input-note-on, .input-note-off{
    background-color: transparent;
    font-size: 25px;
    text-align: center;
    color: black;
    border: none;
    transition: 1s;
    width: 100%;
}

.input-note-on{
    border: 1px solid grey;
}

button{
    border: none;
    margin: 1px;
    padding: 3px;
    border-radius: 3px;
    font-size: 15px;
    cursor: pointer;
    width: 47%;
}


.main-btn{
    position: absolute;
    inset: 0;
    border-radius: 0;
    width: 100px;
    border-right: 2px solid black;
}
